import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Message from "@/views/Message";
import Project from "@/views/Project";
import BlogDetail from "@/views/Blog/Detail";
import { publicPath } from "../path";
console.log("publicPath", publicPath);

export default [
  { name: "Home", path: publicPath, component: Home },
  { name: "Blog", path: publicPath + "/article", component: Blog },
  {
    name: "CategoryBlog",
    path: publicPath + "/article/cate/:categoryId",
    component: Blog,
  },
  {
    name: "BlogDetail",
    path: publicPath + "/article/:id",
    component: BlogDetail,
  },
  { name: "About", path: publicPath + "/about", component: About },
  { name: "Message", path: publicPath + "/message", component: Message },
  { name: "Project", path: publicPath + "/project", component: Project },
];
